export const normalizeTabName = (key) => {
  if (key === 0) {
    return '';
  }
  if (key === 1) {
    return '1/2 ';
  }
  if (key === 2) {
    return '1/4 ';
  }
  if (key === 3) {
    return '1/8 ';
  }
  if (key === 4) {
    return '1/16 ';
  }
  if (key === 5) {
    return '1/32 ';
  }
  return `${key} `;
};
